
/**
 * 初始化
 * @param map
 */
export function addSatellite(map) {
    let option = {
        map: map
    }
    const Satellite = new AMap.TileLayer.Satellite(option);
    Satellite.show()
    return Satellite;
}

/**
 * 隐藏
 */
export function hiddenSatellite(Satellite) {
    Satellite.hide();
}

/**
 * 显示
 */
export function showSatelite(Satellite) {
    Satellite.show();
}
